import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Hidden from '@material-ui/core/Hidden';
import {
  Redirect
} from "react-router-dom";
import axios from 'axios';

const useStyles = makeStyles({
  card: {
    display: 'flex',
  },
  cardDetails: {
    flex: 1,
  },
  cardMedia: {
    width: 160,
  },
});

export default function SanPham(props) {
  const classes = useStyles();
  const { SP ,openDialog} = props;
  const muaHang = (event,data)=>{
    axios.post('https://600e77453bb1d100179df350.mockapi.io/api/product/gioHang',data);
    openDialog('Mua Thành Công !');
   
  }

  return (
    <Grid item xs={12} md={6}>
      <CardActionArea component="a"  onClick={(event)=>{muaHang(event,SP)}} > {/*href="/gioHang"*/}
        <Card className={classes.card}>
          <div className={classes.cardDetails}>
            <CardContent>
              <Typography component="h2" variant="h5">
                {SP.ten}
              </Typography>
              <Typography variant="subtitle1" color="textSecondary">
                {SP.gia} $
              </Typography>
              <Typography variant="subtitle1" paragraph>
                {SP.moTa}
              </Typography>
              <Typography variant="subtitle1" color="primary">
                Mua ...
              </Typography>
            </CardContent>
          </div>
          <Hidden xsDown>
            <CardMedia className={classes.cardMedia} image='https://source.unsplash.com/random'  />
          </Hidden>
        </Card>
      </CardActionArea>
    </Grid>
  );
}

SanPham.propTypes = {
    SP: PropTypes.object,
};